const db = uniCloud.database();

let loadClass=function(pid=0){
	//let res1;
	if(!pid){ //pid0
		//
		db.collection("catagories").get()  //
		.then(res=>{
			console.log(res);
			//

			return res;
		})
		}
	else{
		db.collection("catagories")
		.where({
			pid:pid	 //
		})
		.get()
		.then(res=>{
			console.log(res);
			//
			return res;
		})
	}

}
