const db = uniCloud.database();

//  
let ifrig = function(userInfo,options) {
	//console.log(userInfo);
	db.collection('user').where({
		//unionid
			u_openid: userInfo.openId
		}).get({
			getCount: true
		})
		.then(res => {
			//console.log(res);
			//
			if (res.result.count == 0) {
				db.collection('user').add({
					u_id:'',
					u_openid:userInfo.openId,
					u_name:userInfo.nickName,
					u_face:userInfo.avatarUrl,
					u_random:'',
					u_integral:0,
					u_remainder:0,
					u_regtime:Math.round(new Date() / 1000) //*/
					
				})
				.then((res)=>{
					//  checklogin4
					//else //    (vuexx)
					uni.setStorageSync('SUID', userInfo.openId + '');
					uni.setStorageSync('INTEGRAL',0+'');
					uni.setStorageSync('SNAME', userInfo.nickname + '');
					uni.setStorageSync('SFACE', userInfo.avatarUrl + '');
					uni.hideLoading(); //login
					uni.showToast({
						title: ""
					});
					
					if (options.backtype == 1) {
						uni.redirectTo({
						url: options.backpage
							});
					} else {
						uni.switchTab({
							url: options.backpage
						});
					}
				
				})
				.catch((err) => {
					uni.hideLoading();
					uni.showToast({
						title: ""
					});
					return false;
				})
			}
			else{
				uni.hideLoading();
				uni.showToast({
					title: ""
				});
				uni.setStorageSync('SUID', userInfo.openId + '');
				uni.setStorageSync('INTEGRAL',res.result.data[0].u_integral+'');
				uni.setStorageSync('SNAME', userInfo.nickname + '');
				uni.setStorageSync('SFACE', userInfo.avatarUrl + '');
				if (options.backtype == 1) {
					uni.redirectTo({
					url: options.backpage
						});
				} else {
					uni.switchTab({
						url: options.backpage
					});
				}
			}


			
		})
}

const list = {
	db,
	ifrig
}

export default list
